body{
  background-color: #9266ff;
}

.App{
  background-color: rgba(173, 231, 14, 0.945);
  min-height: 500px;
  width: 400px;
  margin: 40px auto;
}


#to-do-form input{
  background-color: rgb(27, 112, 137);
  border: 0;
  width: 200px;
  height: 50px;
  padding: 0 20px;
  margin: 20px;
  font-size: 18px;
  border-radius: 10px;
  color: whitesmoke;
}

#to-do-form input::placeholder{
  color: rgb(34, 207, 219);
}

#to-do-form input:focus{
  outline:none;
}

#to-do-form button{
  height: 50px;
  width: 80px;
  border-radius: 5px;
  border: 0;
  background-color: rgb(247, 177, 247);
  cursor: pointer;
  font-weight: bolder;
  color: #fff;

}